import React from 'react';




import {
    MDBNavbar, MDBNavbarNav, MDBNavItem, MDBNavLink, MDBNavbarBrand, MDBContainer

} from "mdbreact";

const Header = ({ classes }) => {

    console.log(classes.NavBarHeader)

    return (
        <MDBNavbar dark expand="md" color="unique-color" className={classes.NavBarHeader}  >
            <MDBContainer>
                <MDBNavbarBrand href="/" className={classes.NavBarHeaderTitle} >
                    <h1 className={classes.NavBarHeaderTitle} >Ohel Moche</h1>
                </MDBNavbarBrand>

                <MDBNavbarNav href="/">
                    <img src="https://www.clubsportive.nl/wp-content/uploads/2018/12/logo-example-1.jpg" className="rounded float-left" alt="aligment" height="50" width="100" />
                </MDBNavbarNav>

                <MDBNavbarNav right>
                    <MDBNavItem>
                        <MDBNavLink to="/sign-up" className="waves-effect waves-light">register</MDBNavLink>
                    </MDBNavItem>
                    <MDBNavItem>
                        <MDBNavLink to="/log-in" className="waves-effect waves-light">login</MDBNavLink>
                    </MDBNavItem>
                </MDBNavbarNav>
            </MDBContainer>
        </MDBNavbar>
    )

}


export default Header
